*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
background-color: purple;
font-family: -apple-system,BlinkMacSystemFont,
"Segoe UI",Roboto,Oxygen,Ubuntu,Cantarell,
"Open Sans","Helvetica Neue",sans-serif;
line-height: 1.5;
}
.main_section{
  background-color: #fff;
  border-radius: 5px;
  width: 70%;
  margin: 3rem auto;
}
.question_flex{
  display: flex;
  padding: 1.5rem
}
.question_cont{
  margin: 1.5rem;
  padding: 1rem;
}
.cont_1{
  width: 50%;
  text-transform: capitalize;
}
.cont_2{
  width: 100%;
}
.question_data{
  box-shadow: 2px 2px 10px 2px #eeeeee;
  padding: 1rem;
  margin-bottom: 2rem;
}
.heading_cont{
  display: flex;
  margin-bottom: 1rem;
  align-items: center;
}
.icon_btn{
  margin-left: auto;
  font-size: .8rem;
  border-radius: 100%;
  padding: .8rem;
  background-color: rgb(224, 223, 223);
  color: #ea2c62;
  cursor: pointer;
  border: none;
}
@media (max-width: 900px){
  .question_flex{
    display: block;
    padding: 4rem;
  }
}
@media (max-width: 786px){
  .question_flex{
    display: block;
  }
  .cont_1{
    width: 100%;
  }
  .main_section{
    background-color: #fff;
    border-radius: 5px;
    width: 90%;
    margin: 3rem auto;
  }
}
@media (max-width: 500px){
  .question_flex{
    padding: 1rem;
  }
  .question_cont{
    margin: .5rem;
    padding: .5rem;
  }
}